import BigNumber from 'bignumber.js'
import NftAbi from "../nftAbi.json";
import React from 'react';
import './App.css';
import styled from 'styled-components';
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import {NftContract} from "../constants/index";
import Modal from "./Modal/Modal";
import Select from 'react-select';

import background from '../assets/img/background.png';

const options = [
  { value: '1', label: '1' },
  { value: '2', label: '2' },
  { value: '3', label: '3' },
  { value: '4', label: '4' },
  { value: '5', label: '5' },
  { value: '6', label: '6' },
  { value: '7', label: '7' },
  { value: '8', label: '8' },
  { value: '9', label: '9' },
  { value: '10', label: '10' }
];

const customStyles = {
  option: (provided, state) => ({
    ...provided,
    borderBottom: '1px dotted dark',
    display: 'flex',
    color: state.isSelected ? 'red' : 'blue',
    padding: 10,

  }),
  singleValue: (provided, state) => {
    const opacity = state.isDisabled ? 0.5 : 1;
    const transition = 'opacity 300ms';

    return { ...provided, opacity, transition };
  }
}


class NFTPublic extends React.Component <any, any> {

    constructor(props) {
        super(props)
        this.state = {
          account: '',
          loading: false,
          networkId: 0,
          balanceOf:0,
          pass:"Rune Apes",
          nbNFT: new BigNumber(0),
          nbNFTprice: new BigNumber(8000).div(10**5),
          totalMINT:0,
          mintPrice: new BigNumber(8000).times(10**13),
          maxRuneApes: 7300,
          totalSupply: 0,
          tokenRewardsClaimable:0,
          publicActive: false,
          transaction:'',
          selectedOption: {'value':1,'label':1},
        }
      }


  networkId = 1;
  bigNumber =   "1000000000000000000";
  NftContract = NftContract;
  NftApp = null;
  maxTX = 10;



  async checkpublicActiveTimestamp(){
    let PublicActive = await this.NftApp.methods.activePublic().call()
    this.setState({publicActive: PublicActive})
  }


  async componentDidMount() {
    if(this.props.account !== this.state.account && this.props.web3 !== null)
      await this.loadContract()
  }

  async componentDidUpdate(prevProps) {
      if(this.props.account !== this.state.account && this.props.web3 !== null)
        await this.loadContract()
    }

 async loadContract() {
    this.NftApp = new this.props.web3.eth.Contract(NftAbi, this.NftContract);
    await this.connect()
    await this.checkpublicActiveTimestamp()
  }

  async connect() {

    //console.log(this.props.account)
    if(this.props.account){
        this.setState({ account: this.props.account })
        this.setState({
          totalMINT:  Number(await this.NftApp.methods.balanceOf(this.props.account).call()),
          PublicActive:  (await this.NftApp.methods.activePublic().call()),
          totalSupply:  Number((await this.NftApp.methods.totalSupply().call())),
          nbNFT: new BigNumber(1),
          selectedOption: {'value':1,'label':1},
        })
    }
  }


    updateNFT = (selectedOption) => {
        let value = selectedOption.value

        this.setState({ selectedOption })

        if (new BigNumber(value).eq(new BigNumber(1))){
            this.setState({pass:"Rune Apes"})
            this.setState({nbNFT:new BigNumber(1)})
            this.setState({nbNFTprice:new BigNumber(8000).div(10**5)})
        }
        else if (new BigNumber(value).gt(new BigNumber(1)) && new BigNumber(value).lte(new BigNumber(this.maxTX))){
            this.setState({pass:"Rune Apes"})
            this.setState({nbNFT:new BigNumber(value)})
            this.setState({nbNFTprice: new BigNumber(8000).div(10**5).times(new BigNumber(value))})
        }
        else if (new BigNumber(value).gt(new BigNumber(this.maxTX))){
            this.setState({pass:"Rune Apes"})
            this.setState({nbNFT:new BigNumber(this.maxTX)})
            this.setState({nbNFTprice: new BigNumber(8000).div(10**5).times(new BigNumber(this.maxTX))})
        }
        else{
            this.setState({pass:"Rune Apes"})
            this.setState({nbNFT:new BigNumber(this.maxTX)})
            this.setState({nbNFTprice: new BigNumber(8000).div(10**5).times(new BigNumber(this.maxTX))})
        }
     }


  resolve(transactionHash){
   this.setState({loading:true})
   //console.log(transactionHash,"transactionHash")
   this.setState({transaction:"https://etherscan.io/tx/"+transactionHash})
  }

  reject(err){
   this.setState({loading:false}); 
   //console.log(err)
  }

  loaderModal(receipt){
    this.setState({loading:false}); 
    //console.log(receipt['transactionHash'])
    this.connect()
  }
    

  closeModal(){
    this.setState({loading:false}); 
  }

 async claimToken(){

    let bigvalue = this.state.nbNFT
    let amountETH = new BigNumber(bigvalue).times(this.state.mintPrice)

    await new Promise((resolve, reject) => {
       this.NftApp.methods.publicMint(bigvalue.toNumber())
      .send({from: this.state.account, value: amountETH})
        .once('transactionHash', (transactionHash) => {
            this.resolve(transactionHash)
        })
        .on('confirmation', (confirmationNumber) => {
            //this.resolve(confirmationNumber)
        })
        .on('error', err => this.reject(err))
        .then( receipt =>
            this.loaderModal(receipt)
        );
    })
  }

    render() {
    const { selectedOption } = this.state;

     if(!this.props.web3){
        return(
            <AppWrapper>
            <ConnectWalletWrapper>
               <Wrapper>
                 <Title>
                 Unlock Your Metamask or click Connect.
                  </Title>
               </Wrapper>
            </ConnectWalletWrapper>
            </AppWrapper>
        )
      }
      else
    {
    return (
      <AppWrapper>
                <Modal title="Pending transaction" onClose={() => this.closeModal()} show={this.state.loading}>
                        <ButtonEther><StyledLinkH href={this.state.transaction}  rel="noreferrer" target="_blank">EtherScan transaction</StyledLinkH></ButtonEther>
                </Modal>
        {this.state.publicActive && this.state.totalSupply < this.state.maxRuneApes &&
            <ConnectWalletWrapper>
              <Wrapper>
                <Title>
                    {this.state.totalSupply}/{this.state.maxRuneApes} have been minted.
                </Title>
                <Spacer/>
              </Wrapper>
              <>
              <SelectDiv>
                  <Select
                        value={selectedOption}
                        onChange={this.updateNFT}
                        options={options}
                        styles={customStyles}
                  />
              </SelectDiv>
              <Spacer/>
              <ButtonClaim onClick={this.state.account === '' ? '' : () => this.claimToken()}>{this.state.account === '' ? 'Not connected' : `Mint ${this.state.nbNFT} ${this.state.pass} ${this.state.nbNFTprice}ETH`}</ButtonClaim>
              <Spacer/>
              </>
            </ConnectWalletWrapper>
        }

        {!this.state.publicActive && 

           <ConnectWalletWrapper>
              <Wrapper>
                <Title>
                    Public Access is closed.
                </Title>
                </Wrapper>
            </ConnectWalletWrapper>

        }

        {this.state.publicActive &&  this.state.totalSupply >= this.state.maxRuneApes &&

           <ConnectWalletWrapper>
              <Wrapper>
                <Title>
                    Public Access is closed.
                </Title>
                </Wrapper>
            </ConnectWalletWrapper>

        }

      </AppWrapper>
    );
    }
  }
}


const AppWrapper = styled.div`
    display: flex;
    flex-direction: column;
    min-height: 10vh;
    background-image: url(${background});
    background-size: 100%;
    background-repeat: no-repeat;
    height:100%;
`


const Spacer = styled.div`
flex: 1;
`


